/* Records */

.records {
    position: relative;
    background-color: var(--main-color);
    padding: 5.2rem 0;
}

.records .container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    align-items: center;
    justify-content: center;
    text-align: center;
    grid-column-gap: 1.5rem;
    grid-row-gap: 2rem;
}

.record-circle {
    width: 180px;
    height: 180px;
    border-radius: 50%;
    margin: 0 auto;
    border: 4px solid var(--light-one);
    display: flex;
    flex-direction: column;
    justify-content: center;
    color: var(--light-one);
}

.record-circle.active {
    background-color: var(--light-one);
    color: var(--dark-one);
    box-shadow: 0 0 0 15px rgb(255, 255, 255, 0.5);
}

.number {
    font-weight: 400;
    font-size: 2.35rem;
}

.sub-title {
    font-weight: 400;
    font-size: 1.12rem;
    text-transform: uppercase;
}

.records .square1 {
    left: 8%;
    top: 10%;
}

.records .square2 {
    right: 90px;
    bottom: 70px;
}

.records .xshape {
    top: 45px;
    right: 120px;
}

.records .circle {
    left: 7%;
    bottom: 50px;
}

.records .triangle {
    left: 65%;
    bottom: 35px;
}

.records .wave1 {
    top: 35px;
    right: 30%;
}

.records .wave2 {
    bottom: 40px;
    left: 28%;
}

.records .half-circle {
    top: 30px;
    right: 50%;
}

/* End Records */